function appendZero(i) {
    if (i < 10) {
        i = '0' + i;
    } // add zero in front of numbers < 10
    return i;
}

export function startCountdown(chrono) {
    let seconds = 3 * 60;

    const timer = setInterval(function() {
        seconds -= 1;
        const minutes = Math.trunc(seconds / 60);

        chrono.innerHTML =
            appendZero(minutes) + ' : ' + appendZero(seconds % 60);

        if (seconds === 0) {
            window.testContext.timeEnded = true;
            clearInterval(timer);
        }
    }, 1000);
}
