function cursorValue(curseur, count, additionalText) {
    /* add txt value for type range */
    count.textContent = curseur.value + additionalText;

    window.testContext.setAnswer('@._.' + curseur.value);
    curseur.oninput = function() {
        count.textContent = curseur.value + additionalText;
        window.testContext.setAnswer('@._.' + curseur.value);
    };
}

export { cursorValue };
