function shuffle(a) {
    const cpy = [...a];

    for (let i = cpy.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [cpy[i], cpy[j]] = [cpy[j], cpy[i]];
    }
    return cpy;
}

function getIdsByCategory(questions, category) {
    return questions.filter(q => q.categorie === category).map(q => q.id);
}

function qCorpusAleat(questions) {
    const comprehensionQuestionIds = getIdsByCategory(
        questions,
        'compréhension'
    );
    const knowledgeQuestionIds = getIdsByCategory(questions, 'connaissance');
    const behaviorQuestionIds = getIdsByCategory(questions, 'comportement');
    const cultureQuestionIds = getIdsByCategory(questions, 'culture');

    return shuffle([
        ...shuffle(comprehensionQuestionIds).slice(0, 2),
        ...shuffle(knowledgeQuestionIds).slice(0, 3),
        ...shuffle(behaviorQuestionIds).slice(0, 3),
        ...shuffle(cultureQuestionIds).slice(0, 2)
    ]);

    // return [
    //     ...comprehensionQuestionIds,
    //     ...knowledgeQuestionIds,
    //     ...behaviorQuestionIds
    // ];
}

export { qCorpusAleat };
